@font-face {
  font-family: "r-bold";
  src: url("../fonts/Roboto-Bold.ttf");
}

@font-face {
  font-family: "r-medium";
  src: url("../fonts/Roboto-Medium.ttf");
}

@font-face {
  font-family: "r-regular";
  src: url("../fonts/Roboto-Regular.ttf");
}

@font-face {
  font-family: "r-light";
  src: url("../fonts/Roboto-Light.ttf");
}

@font-face {
  font-family: "r-thin";
  src: url("../fonts/Roboto-Thin.ttf");
}

.container {
  max-width: 1280px;
}

.mini__container {
  max-width: 1040px;
}

.container,
.mini__container {
  max-width: 1280px;
  padding: 0px 16px;
  margin: 0 auto;
}

.primaryBtn {
  padding: 8px 16px;
}

.primaryBtnBig {
  padding: 16px 48px;
}

.primaryBtn,
.primaryBtnBig {
  display: block;
  text-decoration: none;
  background: #7140EB;
  font-size: 15px;
  color: #fff;
  letter-spacing: 0.2px;
  width: fit-content;
  border-radius: 999px;
  transition: 0.3s;
}

.primaryBtn:hover,
.primaryBtnBig:hover {
  background: #5e2fd6;
  box-shadow: 0px 0px 10px #5e2fd6;
}

.secodaryBtn {
  padding: 8px 16px;
}

.secodaryBtnBig {
  padding: 16px 48px;
}

.secodaryBtn,
.secodaryBtnBig {
  display: flex;
  align-items: center;
  gap: 4px;
  text-decoration: none;
  background: transparent;
  border: 1px solid #49464F;
  color: #49464F;
  font-size: 15px;
  cursor: pointer;
  width: fit-content;
  border-radius: 999px;
  transition: 0.3s;
}

.secodaryBtn:hover,
.secodaryBtnBig:hover {
  background: #49464F;
  box-shadow: 0px 0px 10px #5e2fd6;
  color: #fff;
}

.secodaryBtn:hover svg path,
.secodaryBtnBig:hover svg path {
  fill: #fff;
}

.light__text__title {
  color: #2E2C34;
}

.dark__text__title {
  color: #49464F;
}

.dark__text__title,
.light__text__title {
  font-weight: 400;
  font-size: min(67px, max(33.5px + 2vw));
}

#burger-menu {
  cursor: pointer;
  height: 27px;
  width: 27px;
  overflow: visible;
  position: relative;
  z-index: 2;
}

#burger-menu span,
#burger-menu span:before,
#burger-menu span:after {
  background: #fff;
  display: block;
  height: 4px;
  opacity: 1;
  position: absolute;
  transition: 0.3s ease-in-out;
}

#burger-menu span:before,
#burger-menu span:after {
  content: "";
}

#burger-menu span {
  right: 0px;
  top: 13px;
  width: 27px;
}

#burger-menu span:before {
  left: 0px;
  top: -10px;
  width: 16px;
}

#burger-menu span:after {
  left: 0px;
  top: 10px;
  width: 20px;
}

#burger-menu.close span {
  transform: rotate(-45deg);
  top: 13px;
  width: 27px;
}

#burger-menu.close span:before {
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}

#burger-menu.close span:after {
  top: 0px;
  left: 0;
  transform: rotate(90deg);
  opacity: 0;
  width: 0;
}

