header {
  min-height: 800px;
  background-image: url("../../img/header-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header nav .nav__left img {
  display: block;
}

header nav .nav__mobile {
  display: none;
}

header nav .nav__right {
  display: flex;
  align-items: center;
  gap: 64px;
}

header nav .nav__right .nav__menu {
  margin: 0;
  display: flex;
}

header nav .nav__right .nav__menu li {
  list-style-type: none;
}

header nav .nav__right .nav__menu li a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #fff;
  min-height: 100px;
  padding: 0px 16px;
  transition: 0.3s;
}

header nav .nav__right .nav__menu li a:hover {
  background: #7140EB;
}

header nav .nav__right .nav__lang {
  display: flex;
  gap: 16px;
}

header nav .nav__right .nav__lang a {
  display: block;
  text-decoration: none;
  color: #fff;
}

header nav .nav__right .nav__lang a:hover {
  text-decoration: underline;
}

header .header__content {
  max-width: 540px;
  padding: 128px 10px 128px 10px;
  margin: 0 auto;
  text-align: center;
}

header .header__content h1 {
  font-size: min(99px, max(40px + 2vw));
  letter-spacing: 5px;
  color: #fff;
  font-weight: normal;
  margin: 0;
}

header .header__content p {
  margin: 64px 0px;
  font-size: 21px;
  color: #fff;
  letter-spacing: 0.2px;
}

header .header__content a {
  width: 183px;
  height: 42px;
  margin: 0 auto;
  line-height: 26px;
}

main .how__its__work__section {
  padding: 64px 0px;
  position: relative;
}

main .how__its__work__section .mini__container {
  max-width: 1040px;
}

main .how__its__work__section .mini__container h2 {
  width: 100%;
  margin: 0px 0px 24px 0px;
}

main .how__its__work__section .mini__container .how__its__work__list {
  position: relative;
  z-index: 5;
}

main .how__its__work__section .mini__container .how__its__work__list ol {
  padding: 0;
  margin-bottom: 48px;
}

main .how__its__work__section .mini__container .how__its__work__list ol li {
  color: #2E2C34;
  margin-bottom: 24px;
  list-style-position: inside;
  font-size: 28px;
  line-height: 34px;
}

main .how__its__work__section .mini__container .how__its__work__list ol li:last-child {
  margin-bottom: 0;
}

main .how__its__work__section .mini__container .how__its__work__cover {
  position: absolute;
  z-index: 1;
  top: 70px;
  right: 0;
}

main .how__its__work__section .mini__container .how__its__work__cover img {
  display: block;
}

main .move__your__way__section {
  background: #F5F5F7;
  padding: 64px 0px;
}

main .move__your__way__section h2 {
  margin: 0px 0px 24px 0px;
}

main .move__your__way__section p {
  font-size: 20px;
  line-height: 25px;
  color: #49464F;
  margin: 0px;
}

main .move__your__way__section .move__your__way__items {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 48px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item {
  background: #fff;
  padding: 24px;
  border-radius: 16px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__cover {
  max-width: 286;
  max-height: 227px;
  overflow: hidden;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__cover a {
  display: block;
  margin-bottom: 16px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__cover a img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__header {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__header p {
  font-size: 16px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__header p.offer {
  color: #7140EB;
  background: #F1EEF8;
  border-radius: 20px;
  padding: 1px 10px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__title__subtitle {
  margin: 24px 0px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__title__subtitle h4 {
  margin: 0px 0px 16px 0px;
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__title__subtitle h4 a {
  text-decoration: none;
  color: #2E2C34;
  font-size: min(29px, max(15px + 2vw));
}

main .move__your__way__section .move__your__way__items .move__your__way__item .mywi__title__subtitle p {
  color: #2E2C34;
}

main .move__your__way__section .move__your__way__items .mywi__btn a {
  justify-content: center;
  width: 100%;
}

body {
  font-family: "r-regular", sans-serif;
}

@media screen and (max-width: 1120px) {
  main .how__its__work__section .mini__container .how__its__work__cover {
    width: 70vw;
  }

  main .how__its__work__section .mini__container .how__its__work__cover img {
    width: inherit;
  }
}

@media screen and (max-width: 1024px) {
  main .move__your__way__section .move__your__way__items {
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 768px) {
  .dark__text__title,
  .light__text__title {
    margin: 0px 0px 48px 0px !important;
    text-align: center;
  }

  .subtitle {
    text-align: center;
  }

  header nav {
    min-height: 100px;
  }

  header nav .nav__mobile {
    display: block;
  }

  header nav .nav__right {
    position: fixed;
    flex-direction: column;
    align-items: flex-start;
    top: 0;
    left: -5000px;
    width: 250px;
    height: 100vh;
    background: #fff;
    gap: 32px;
    transition: left 0.3s;
  }

  header nav .nav__right.active__nav {
    transition: left 0.3s;
    left: 0px;
  }

  header nav .nav__right .nav__menu {
    flex-direction: column;
    gap: 0px;
    width: 100%;
    border-bottom: 0.5px solid #eee;
  }

  header nav .nav__right .nav__menu li a {
    padding: 0px 30px;
    color: #000;
  }

  header nav .nav__right .nav__menu li a:hover {
    color: #fff;
  }

  header nav .nav__right .nav__lang {
    padding: 0px 30px;
  }

  header nav .nav__right .nav__lang a {
    color: #49464F;
  }

  header nav .nav__right .nav__btn {
    padding: 0px 30px;
    width: 100%;
  }

  header nav .nav__right .nav__btn a {
    width: 100%;
    text-align: center;
  }

  header {
    min-height: unset;
  }

  header .header__content {
    padding: 64px 10px 64px 10px;
  }

  header .header__content p {
    margin: 32px 0px;
  }

  main .how__its__work__section .mini__container .how__its__work__cover {
    display: none;
  }

  main .how__its__work__section .mini__container .how__its__work__list {
    text-align: center;
  }

  main .how__its__work__section .mini__container .how__its__work__list a {
    margin: 0 auto;
  }
}